@import "@/assets/global.scss";




































































.article {
  display: flex;
  justify-content: space-between;
  margin: 16px;

  > span {
    display: flex;
    align-items: center;
  }
}

.labelList {
  padding: 0 16px;
  margin-bottom: 16px;
  max-height: 40px;
  overflow-y: hidden;
  box-sizing: border-box;
  transition: max-height 0.5s ease-in-out;
}

.expandList {
  max-height: 500px;
}

.bar {
  width: 100%;
  border: 1px solid #E9E9E8;
}
