@import "@/assets/global.scss";








































.themeSelect {
  background: #FFFFFF;
  height: auto;
  max-height: 40px;
  padding: 3px 15px;
  margin: 16px;
  border: 1px solid #FCA311;
  border-radius: 24px;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: max-height 0.5s ease-in-out;

  > span {
    width: 100%;
    padding: 9px 0 9px 9px;
    box-sizing: border-box;
    text-align: left;
  }

  > span:first-child {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.expandList {
  max-height: 500px;
}
